/* .main-button */
.main-button {
    border: none;
    outline: none;
    color: var(--main-button-text-color);
    font-size: 1rem;
    font-weight: 500;
    padding: .5rem 1.2rem .5rem 1.2rem;
    background-color: var(--main-element-color);
    border-radius: .5rem;
    cursor: pointer;
  }
  .main-button:focus, .main-button:active, .main-button:hover{
    border:none;
    outline: none;
  }
  .main-button:hover, .oval-button:hover{
    background-color: var(--main-button-background-color);
  }
  .main-button:focus, .oval-button:focus{
    background-color: var(--main-button-background-color);
    outline: 3px solid var(--main-element-color);
  }
  .main-button:active, .oval-button:active{
    background-color: var(--main-element-color);
  }
/* .secondary-button */
  .secondary-button:focus, .secondary-button:active, .secondary-button:hover{
    border:none;
    outline: none;
  }
  .secondary-button:hover{
    outline: 1px solid var(--main-button-background-color);
  }
  .secondary-button:focus{
    outline: 2px solid var(--main-button-background-color);
  }
  .secondary-button:active {
    outline: 2px solid var(--main-button-background-color);
  }
/* .oval-button */
  .oval-button {
    border: none;
    outline: none;
    color: var(--main-button-text-color);
    font-size: 1rem;
    font-weight: 500;
    padding: .5rem 1rem;
    background-color: var(--main-element-color);
    border-radius: 60px;
    cursor: pointer;
  }
  @media only screen and (max-width: 390px){
    .oval-button{
      font-size: .8rem; 
    } 
  }