@import url('https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&family=Poppins:wght@200;300;400;500;600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Sono:wght@200;300;400;500;600&display=swap');
@import "./styles/warm-cold-theme";

* {
  box-sizing: border-box;
  //font-family: 'Open Sans', sans-serif;
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  //content-visibility: visible;
}
body{
  text-align: center;
  background-color: var(--main-theme-color);
  background-image: linear-gradient(43deg, var(--main-theme-color) 0%, var(--secondary-gradient-color) 46%, var(--accent-gradient-color) 100%);
  overflow-x:hidden;
  font-size: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.main-container { 
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center; 
  border-radius: 1rem;
  border: none;
  max-width: 1000px;
  width: 90vw;
  flex: 1;
}